import React from 'react';
import { nav } from '../components';
import { VPage } from '../vm';
import { CLogin } from './CLogin';

export class VLogout extends VPage<CLogin> {
	private onLogout: ()=>void;

	init(param: any) {
		this.onLogout = param;
	}
	header() {return this.isWebNav === true? false : '';}
	footer() {
		return <div className="mt-5 text-center justify-content-center">
			<button className="btn btn-outline-warning" onClick={nav.resetAll}></button>
		</div>;
	}
	protected get back(): 'close' | 'back' | 'none' {return 'close'}
	content() {
		return <div className="m-5 border border-info bg-white rounded-3 p-5 text-center">
			<div></div>
			<div className="mt-5 text-center">
				<button className="btn btn-danger" onClick={this.onLogout}></button>
			</div>
		</div>;
	}
}
